<template>
  <van-row style="height:100%">
    <!-- 最外层 -->
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">任务审核</p>
      </van-col>
    </van-row>
    <!-- 主体内容 -->

    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">任务编号：</van-col>
          <span class="dt-item-content">{{order.TaskCode}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">任务标题：</van-col>
          <span class="dt-item-content">{{order.TaskTitle}}</span>
        </van-row>

        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">任务内容：</van-col>

          <span class="dt-item-content">{{order.TaskContent}}</span>

        </van-row>

        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">任务状态：</van-col>
          <span class="dt-item-content">{{order.TaskStatus | filteOrderTStatus}}</span>
        </van-row>

        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交人：</van-col>
          <span class="dt-item-content">{{order.SubmitUserName}}</span>
        </van-row>
        <van-row class="dt-top">
          <van-col :span="8"
                   class="dt-item-title">提交时间：</van-col>
          <span class="dt-item-content">{{order.SubmitTime | filteDate}}</span>
        </van-row>
      </van-row>
      <!-- 按钮 -->
      <van-row class="dt-button-box">

        <div class="dt-line"></div>

        <van-row>
          <van-col :span="12"
                   class="dt-button">
            <van-button round
                        style="width:100px"
                        type="info"
                        @click="reject">不通过</van-button>
          </van-col>
          <van-col :span="12"
                   class="dt-button">
            <van-button round
                        style="width:100px"
                        type="info"
                        @click="submit">通过</van-button>
          </van-col>
        </van-row>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions,
  UpdateItem,
} from "@/api/testApi";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      imgSrc: require('../../../assets/img/工单背景.png'),
      order: {
      },
      // 通过页面是否显示
      redeployShow: false,
      //通过选择器选择的选项
      redeployValue: '',
      //工单类型
      // 驳回页面是否显示
      rejectShow: false,
      //驳回选择器选择的选项
      rejectValue: '',
      //工单类型
      // 选择日期
      // 工单表格
    };
  },
  filters: {
    filteDate (date) {
      if (date) {
        return date.replace("T", " ");
      }
    },
    filteOrderTStatus (data) {
      let all = {
        1: '待审核',
        2: '已审核',
        3: '已驳回',
        4: '已完成',
      }
      return all[data]
    },
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getTestList()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 任务查询
    getTestList () {
      GetPageContentByConditions({ ID: this.$route.query.ID }).then(response => {
        if (response.Data.ContentList) {
          this.order = response.Data.ContentList[0]
        } else {
          this.order = []
        }
      })
    },
    formatDate (date) {
      return `${date.getMonth() + 1}/${date.getDate()}`;
    },
    onConfirm (date) {
      const [start, end] = date;
      this.show = false;
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
    },
    //不通过显示弹框
    reject () {
      // this.rejectShow = true
      this.$dialog.confirm({
        message: '确定驳回？',
      }).then(() => {
        this.order.TaskStatus = 3
        UpdateItem(this.order).then(response => {
          this.$router.push({ name: 'MTTask' })
        })
      }).catch(() => {
        // on cancel
      });
    },
    // 通过跳转显示弹框
    submit () {
      // this.redeployShow = true
      this.$dialog.confirm({
        message: '确定通过？',
      }).then(() => {
        this.order.TaskStatus = 2
        UpdateItem(this.order).then(response => {
          if (this.$route.query.modelType === 1) {
            this.$router.push({ name: 'MTTask' })
          } else if (this.$route.query.modelType === 2) {
            this.$router.push({ name: 'TMTask' })
          }
        })
      })
    },
    back () {
      this.$store.commit("active", 0)
      javascript: history.back(-1);
    },
  }
}
</script>
<style lang='scss' scoped>
@import "../../../styles/dimens";
//@import url(); 引入公共css类
$noticeBarHeight: px2rem(50);
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
.dt-content {
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  // // background-image: url("../../../assets/img/工单背景.png");
  background-image: url("../../../assets/img/背景图.png");
  background-size: 100% 100%;
  // background-attachment: fixed;
  // background-position: 0 px2rem(74);
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    margin: 0 3%;
    margin-top: px2rem(29);
    overflow-y: auto;
    .dt-item-title {
      width: px2rem(90);
      margin-left: px2rem(30);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      color: #4c4c4c;
      font-weight: 500;
      line-height: px2rem(23);
      text-align: left;
    }
    .dt-item-content {
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: 400;
      text-align: left;
      color: #707070;
      line-height: px2rem(23);
      width: px2rem(200);
      float: right;
      padding-right: px2rem(25);
    }
  }
  .dt-top {
    margin-top: px2rem(6);
  }
  .dt-button-box {
    position: fixed;
    display: flex;
    flex-direction: column;
    bottom: px2rem(4.5);
    margin: 0 px2rem(24);
    .dt-line {
      width: px2rem(326.5);
      background-color: #edf1f5;
      // background-color: red;
      height: px2rem(0.5);
    }
    .dt-button {
      margin-top: px2rem(5);
    }
  }
}

//整个遮罩层
.dt-wrapper {
  position: fixed;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  // 文字显示框
  .dt-wrapper-content {
    width: 100%;
    margin-top: px2rem(300);
    height: 25%;
    background-color: rgb(255, 255, 255);
    //确定通过吗
    .dt-wrapper-tip {
      height: px2rem(80);
      line-height: px2rem(80);
    }
    //按钮框
    .dt-redeploy-button-box {
      width: 100%;
      margin: px2rem(30)  0;
      // 按钮
      .dt-redeploy-button {
        width: 50%;
        float: left;
        margin: px2rem(10px);
      }
    }
  }
}
</style>